<template>
    <div id="balance" style="padding:5% 20%;width:50%;text-align:center">
      <el-form :model="form" label-width="100px" style="padding:4% 20% 5% 15%;background-color:#fff;border-radius:8px;">
        <div style="font-size:17px;margin:7% 0px;">
            <b>余额查询</b>
        </div>
        <el-form-item label="银行名称：">
            <el-input v-model="form.bankId" placeholder="请输入银行名称"></el-input>
        </el-form-item>
        <el-form-item label="用户名称：">
            <el-input v-model="form.accountId" placeholder="请输入用户名称"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-button type="primary" @click="queryBalance">查询余额</el-button>
            <el-button type="success" @click="storage">保存记录</el-button>
        </el-form-item>
        <el-form-item label="账户余额：">
            <label><b>{{balance}}</b></label>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
export default {
  name: 'Balance',
  data () {
    return {
      form: {},
      balance: null
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var localStr = localStorage.getItem('balance')
      if (localStr === null) {
        return
      }
      this.form = JSON.parse(localStorage.getItem('balance'))
    },
    storage () {
      localStorage.setItem('balance', JSON.stringify(this.form))
    },
    queryBalance () {
      var that = this
      this.$axios.get('/v1/account/' + this.form.accountId + '/bank/' + this.form.bankId)
        .then(function (response) {
          console.log(response)
          if (response.data.validCode === 0) {
            that.$message.success('查询成功')
            that.balance = response.data.balance
          } else {
            that.$message.error(response.data.msg)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>



